            // { name: "", value: 15477.48 },
            // { name: "", value: 31686.1 },
            // { name: "", value: 6992.6 },
            // { name: "", value: 44045.49 },
            // { name: "", value: 40689.64 },
            // { name: "", value: 37659.78 },
            // { name: "", value: 45180.97 },
            // { name: "", value: 55204.26 },
            // { name: "", value: 21900.9 },
            // { name: "", value: 4918.26 },
            // { name: "", value: 5881.84 },
            // { name: "", value: 4178.01 },
            // { name: "", value: 2227.92 },
            // { name: "", value: 2180.98 },
            // { name: "", value: 9172.94 },
            // { name: "", value: 3368 },
            // { name: "", value: 806.98 }
          ],
          nameMap: {
            // "Central and Western": "",
            // Eastern: "",
            // Islands: "",
