<template>
  <div id="map-chart-card-component">
    <div class="top-container">
      <h3 class="title">{{ title }}</h3>
      <div class="expand-button" @click="onExpandClick">
        <img src="@/assets/images/icons/png/expand.png" alt="" />
      </div>
    </div>
    <div class="chart-container">
      <div :id="'map-chart-main-' + title" class="chart-main"></div>
    </div>

    <el-dialog :title="title" v-model="dialogVisible" width="50%">
      <div class="dialog-container">
        <div :id="'map-chart-dialog-' + title" class="chart-dialog"></div>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, toRefs, onMounted, nextTick } from "vue";
import * as echarts from "echarts/core";
import { MapChart, MapSeriesOption } from "echarts/charts";
import { GridComponent, GridComponentOption } from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";
import World from "@/constants/echarts-map-world.json";

type ECOption = echarts.ComposeOption<MapSeriesOption | GridComponentOption>;

echarts.use([MapChart, GridComponent, CanvasRenderer]);

// @ts-ignore
echarts.registerMap("world", World);

const Component = defineComponent({
  name: "LineChart",
  props: {
    title: {
      type: String,
      default: ""
    },
    data: {
      type: Object,
      default: () => ({})
    }
  },
  setup(props) {
    const { title } = toRefs(props);
    const dialogVisible = ref(false);

    const option: ECOption = {
      series: [
        {
          // name: "18",
          type: "map",
          map: "world",
          data: [
            // { name: "", value: 20057.34 },
            // { name: "", value: 15477.48 },
            // { name: "", value: 31686.1 },
            // { name: "", value: 6992.6 },
            // { name: "", value: 44045.49 },
            // { name: "", value: 40689.64 },
            // { name: "", value: 37659.78 },
            // { name: "", value: 45180.97 },
            // { name: "", value: 55204.26 },
            // { name: "", value: 21900.9 },
            // { name: "", value: 4918.26 },
            // { name: "", value: 5881.84 },
            // { name: "", value: 4178.01 },
            // { name: "", value: 2227.92 },
            // { name: "", value: 2180.98 },
            // { name: "", value: 9172.94 },
            // { name: "", value: 3368 },
            // { name: "", value: 806.98 }
          ],
          nameMap: {
            // "Central and Western": "",
            // Eastern: "",
            // Islands: "",
            // "Kowloon City": "",
            // "Kwai Tsing": "",
            // "Kwun Tong": "",
            // North: "",
            // "Sai Kung": "",
            // "Sha Tin": "",
            // "Sham Shui Po": "",
            // Southern: "",
            // "Tai Po": "",
            // "Tsuen Wan": "",
            // "Tuen Mun": "",
            // "Wan Chai": "",
            // "Wong Tai Sin": "",
            // "Yau Tsim Mong": "",
            // "Yuen Long": ""
          }
        }
      ]
    };

    const onExpandClick = async () => {
      dialogVisible.value = true;
      await nextTick();
      const chart = echarts.init(
        document.getElementById("map-chart-dialog-" + title.value)!
      );
      chart.setOption(option);
    };

    onMounted(() => {
      const chart = echarts.init(
        document.getElementById("map-chart-main-" + title.value)!
      );
      chart.setOption(option);
    });

    return {
      dialogVisible,

      onExpandClick
    };
  }
});

export default Component;
</script>

